export default {
	data() {
		return {
			tagMenuOpen:false,
			imgList:[]
		}
	},
	methods: {
		tagMenu:function(){
			if(this.tagMenuOpen){
				this.tagMenuOpen = false;
			}else{
				this.tagMenuOpen = true;
			}
		},
		selectPic:function(){
			let that = this;
			let count = 9 - that.imgList.length;
			uni.chooseImage({
				count:count,
				sizeType:['original','compressed'],
				sourceType:['album','camera'],
				success:function(res){
					console.log(res);
					for (let item in res.tempFilePaths) {
						that.imgList.push(res.tempFilePaths[item]);
					}
				}
			})
		},
		//图片预览;
		previewPic:function(index){
			//index为当前选择照片的索引;
			let that = this;
			uni.previewImage({
				current:index,
				urls:that.imgList,
				success:function(res){
					console.log('图片预览成功');
					console.log(res);
				}
			})
		},
		deletePic:function(index){
			
			console.log('index:'+index);
			let that = this;
			//index为当前选择删除的照片索引;
			//弹出提示框;
			uni.showModal({
				title:'操作提示',
				content:'取消上传该图片?',
				success:function(res){
					//用户确认删除;
					if(res.confirm){
						//删除imgList当前索引为index的项;
						that.imgList.splice(index,1);
						
					}
				}
			})
		}
		
	}
}
